      <template v-slot:append v-if="isLogged">
        <div class="pa-2">
          <v-btn @click="logout" block color="red" tile dark>Logout</v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-app-bar app color="indigo" dark>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>Application</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container class="fill-height" fluid align-start>
        <v-row align="start" justify="start">
          <v-col>
            <router-view @logged="logged"></router-view>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  props: {
    source: String
  },
  data: () => ({
    drawer: null,
    isLogged: false
  }),
  created() {
    if (localStorage.token) {
      this.isLogged = true;
    }
  },
  methods: {
    logout() {
      delete localStorage.token;
      delete localStorage.type;
      this.isLogged = false;
      this.$router.push("Login");
    },

    logged(token, type) {
      this.isLogged = true;
      localStorage.token = token;
      localStorage.type = type;
    }
  }
};
</script>

